@import url("https://fonts.googleapis.com/css?family=Quicksand|Raleway:300,400|Roboto:300");
body, p, h1, h2, h3, h4, h5, label, span {
  font-family: 'Raleway', sans-serif; }

.daNav {
  margin-bottom: 0;
  margin-top: 2px; }
  .daNav .mobileHr {
    display: none; }
  .daNav li a {
    color: #a5a3a3; }
  .daNav .navbar-brand {
    height: 70px;
    padding-top: 19px; }
    .daNav .navbar-brand .newLogo {
      color: grey;
      font-family: 'Quicksand', sans-serif;
      font-size: 27px;
      margin-top: 5px;
      margin-bottom: 0;
      margin-right: 10px; }
  .daNav .nav-link {
    padding: 1rem 2rem;
    font-weight: 400; }
  .daNav .navbar-right a {
    padding-top: 24px;
    padding-bottom: 24px;
    font-size: 18px;
    cursor: pointer;
    font-weight: 100; }

#pgArtists .jumbotron {
  padding-bottom: 10px;
  padding-top: 0;
  background-color: #e5f0fb; }
#pgArtists .artistBlock {
  display: block;
  height: 12em;
  margin: auto;
  margin-bottom: 1em;
  padding: 1em;
  border-bottom: 1px solid #6fb5fb;
  max-width: 900px; }
  #pgArtists .artistBlock .featuredImg {
    max-height: 10em;
    display: block;
    margin: auto;
    border-radius: 50%; }
  #pgArtists .artistBlock .rightCol .publicName {
    margin-top: 26px;
    font-size: 22px;
    margin-bottom: 5px; }
  #pgArtists .artistBlock .rightCol .fname {
    margin-bottom: 5px; }

#pgStory .jumbotron {
  background: url("./assets/imgs/bg-t1.jpg");
  background-size: contain;
  color: white; }
  #pgStory .jumbotron h2 {
    color: white;
    font-size: 33px;
    margin-top: 2.7em; }
  #pgStory .jumbotron h4 {
    margin-bottom: 2.5em;
    font-size: 25px; }
  #pgStory .jumbotron input {
    width: 33%;
    display: block;
    margin: auto;
    margin-bottom: 2em;
    background: #818181a8;
    color: white; }
  #pgStory .jumbotron ::-webkit-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #ddd;
    opacity: 1;
    /* Firefox */ }
  #pgStory .jumbotron :-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #ddd;
    opacity: 1;
    /* Firefox */ }
  #pgStory .jumbotron ::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #ddd;
    opacity: 1;
    /* Firefox */ }
  #pgStory .jumbotron ::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #ddd;
    opacity: 1;
    /* Firefox */ }
  #pgStory .jumbotron .btn {
    background: #55bee6;
    margin-bottom: 4em; }
#pgStory .twoColumnBullets {
  margin-bottom: 2em; }
  #pgStory .twoColumnBullets h3 {
    margin-bottom: 1.7em; }
  #pgStory .twoColumnBullets .leftCol {
    border-right: 1px solid #bfbfbf; }
  #pgStory .twoColumnBullets .bullet {
    margin-bottom: 2.5em; }
    #pgStory .twoColumnBullets .bullet .fa {
      color: grey;
      font-size: 2.2em; }
    #pgStory .twoColumnBullets .bullet p {
      max-width: 80%;
      display: block;
      margin: auto; }

#footer {
  padding-top: 3em;
  padding-bottom: 3em;
  background: #55bee754; }
  #footer .igFa {
    background: #3a3a3aa8;
    width: 33px;
    height: 33px;
    display: block;
    margin: auto;
    border-radius: 50%; }
    #footer .igFa .fa {
      color: white;
      margin-top: 8px;
      font-size: 1.3em;
      margin-left: 1px; }

@media (max-width: 600px) {
  .daNav {
    text-align: center; }
    .daNav .navbar-brand {
      padding: 7px 14px;
      height: 41px;
      width: 100%; }
    .daNav .navbar-right a {
      padding: 3px 14px;
      font-size: 16px; }
    .daNav .mobileHr {
      display: inherit;
      margin-top: 0;
      margin-bottom: 10px;
      width: 60%; }

  #pgArtists .artistBlock {
    height: 19em !important; }
    #pgArtists .artistBlock .rightCol p {
      margin-bottom: 0; }
    #pgArtists .artistBlock .rightCol .publicName {
      margin-top: 1em;
      font-size: 18px;
      margin-bottom: 5px !important; }

  #pgStory .jumbotron h2 {
    font-size: 28px;
    margin-top: 1.7em; }
  #pgStory .jumbotron h4 {
    margin-bottom: 1.5em;
    font-size: 20px; }
  #pgStory .jumbotron input {
    width: 70%; }
  #pgStory .twoColumnBullets {
    margin-bottom: 1em; }
    #pgStory .twoColumnBullets .leftCol {
      border-right: none; }
    #pgStory .twoColumnBullets .rightCol {
      border-top: 1px solid #bfbfbf; } }



